<template><section><h1>描述</h1>
<p>scroll组件用于页面有插件，原生滚动条会导致插件闪烁，或者因为原生滚动条不够美观时的解决方案。</p>
<p>注意事项为组件高度一定要是可计算的，若为100%则他的父容器高度需要是可计算的。</p>
<h1>效果展示</h1>
<template>
  <div>
    <div style="height:200px">
      <bs-scroll class="inb" style="width:200px" ref="scroller">
        <ul>
          <li v-for="index in count" :key="index">{{index}}</li>
        </ul>
      </bs-scroll>
      <div class="inb">
        <v-btn @click="count++">+</v-btn>
        <v-btn @click="count--">-</v-btn>
        <v-btn @click="getT">getTop</v-btn>
        <v-btn @click="setT">setTop</v-btn>
      </div>
    </div>
  </div>
</template>


<h1>代码示例</h1>
<pre v-pre=""><code v-pre="" class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">div</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">div</span> <span class="hljs-attr">style</span>=<span class="hljs-string">"height:200px"</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">bs-scroll</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"inb"</span> <span class="hljs-attr">style</span>=<span class="hljs-string">"width:200px"</span> <span class="hljs-attr">ref</span>=<span class="hljs-string">"scroller"</span>&gt;</span>
        <span class="hljs-tag">&lt;<span class="hljs-name">ul</span>&gt;</span>
          <span class="hljs-tag">&lt;<span class="hljs-name">li</span> <span class="hljs-attr">v-for</span>=<span class="hljs-string">"index in count"</span> <span class="hljs-attr">:key</span>=<span class="hljs-string">"index"</span>&gt;</span>{{index}}<span class="hljs-tag">&lt;/<span class="hljs-name">li</span>&gt;</span>
        <span class="hljs-tag">&lt;/<span class="hljs-name">ul</span>&gt;</span>
      <span class="hljs-tag">&lt;/<span class="hljs-name">bs-scroll</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">div</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"inb"</span>&gt;</span>
        <span class="hljs-tag">&lt;<span class="hljs-name">v-btn</span> <span class="hljs-attr">@click</span>=<span class="hljs-string">"count++"</span>&gt;</span>+<span class="hljs-tag">&lt;/<span class="hljs-name">v-btn</span>&gt;</span>
        <span class="hljs-tag">&lt;<span class="hljs-name">v-btn</span> <span class="hljs-attr">@click</span>=<span class="hljs-string">"count--"</span>&gt;</span>-<span class="hljs-tag">&lt;/<span class="hljs-name">v-btn</span>&gt;</span>
        <span class="hljs-tag">&lt;<span class="hljs-name">v-btn</span> <span class="hljs-attr">@click</span>=<span class="hljs-string">"getT"</span>&gt;</span>getTop<span class="hljs-tag">&lt;/<span class="hljs-name">v-btn</span>&gt;</span>
        <span class="hljs-tag">&lt;<span class="hljs-name">v-btn</span> <span class="hljs-attr">@click</span>=<span class="hljs-string">"setT"</span>&gt;</span>setTop<span class="hljs-tag">&lt;/<span class="hljs-name">v-btn</span>&gt;</span>
      <span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>
    <span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>
  <span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
<span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
  data() {
    <span class="hljs-keyword">return</span> {
      <span class="hljs-attr">count</span>: <span class="hljs-number">11</span>
    }
  },
  <span class="hljs-attr">watch</span>: {
    count(a) {
      <span class="hljs-keyword">if</span> (a &lt; <span class="hljs-number">0</span>) {
        <span class="hljs-keyword">this</span>.count = <span class="hljs-number">0</span>
      }
      <span class="hljs-keyword">this</span>.update()
    }
  },
  <span class="hljs-attr">methods</span>: {
    getT() {
      <span class="hljs-built_in">window</span>.alert(<span class="hljs-keyword">this</span>.$refs.scroller.getScrollTop())
    },
    setT() {
      <span class="hljs-keyword">this</span>.$refs.scroller.setScrollTop(<span class="hljs-number">0</span>)
    },
    update() {
      <span class="hljs-keyword">this</span>.$refs.scroller.update()
    }
  }
}
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>

</code></pre>
<h1>props</h1>
<table class="table table-striped table-bordered"><thead>
<tr>
<th>属性名称</th>
<th>描述</th>
<th>类型</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>height</td>
<td>组件限定高度</td>
<td>String</td>
<td>100%</td>
</tr>
<tr>
<td>wheelSpeed</td>
<td>滚动速度</td>
<td>Number</td>
<td>40</td>
</tr>
<tr>
<td>option</td>
<td>组件的option对象（可选）, 对象属性如下表格</td>
<td>Object</td>
<td>无</td>
</tr>
</tbody>
</table>
<h3>option属性列表</h3>
<table class="table table-striped table-bordered"><thead>
<tr>
<th>属性名称</th>
<th>描述</th>
<th>类型</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>color</td>
<td>滚动条颜色</td>
<td>String</td>
<td>#909090</td>
</tr>
<tr>
<td>background</td>
<td>滚动条底色</td>
<td>String</td>
<td>#e3e3e3</td>
</tr>
<tr>
<td>width</td>
<td>滚动条宽度</td>
<td>String</td>
<td>10px</td>
</tr>
<tr>
<td>margin</td>
<td>滚动条边距</td>
<td>String</td>
<td>0px</td>
</tr>
</tbody>
</table>
<p>以上属性还可以通过设置全局变量来实现
如:</p>
<pre v-pre=""><code v-pre="" class="language-javascript">Vue.ScrollOption = {
  <span class="hljs-attr">color</span>: <span class="hljs-string">'red'</span>
}
</code></pre>
<p>通过全局变量设置的样式会影响所有的默认样式
建议在main中定义全局样式</p>
<h1>events</h1>
<table class="table table-striped table-bordered"><thead>
<tr>
<th>事件名称</th>
<th>描述</th>
<th>参数</th>
</tr>
</thead>
<tbody>
<tr>
<td>on-scroll</td>
<td>滚动事件</td>
<td>无</td>
</tr>
<tr>
<td>scroll-top</td>
<td>滚动到顶部</td>
<td>无</td>
</tr>
<tr>
<td>scroll-bottom</td>
<td>滚动到底部</td>
<td>无</td>
</tr>
</tbody>
</table>
<h1>methods</h1>
<table class="table table-striped table-bordered"><thead>
<tr>
<th>方法名称</th>
<th>描述</th>
<th>参数</th>
<th>返回值</th>
</tr>
</thead>
<tbody>
<tr>
<td>getScrollTop</td>
<td>获得scrolltop的值</td>
<td>无</td>
<td>scrolltop的值</td>
</tr>
<tr>
<td>setScrollTop</td>
<td>设置scrolltop的值</td>
<td>scrolltop的值</td>
<td>无</td>
</tr>
<tr>
<td>update</td>
<td>内容高度变化时调用此方法重新计算区域(简单结构会自动调用)</td>
<td>无</td>
<td>无</td>
</tr>
</tbody>
</table>
</section></template>
<style>
.inb {
  display: inline-block;
}
</style>
<script>
export default {
  data() {
    return {
      count: 15
    }
  },
  watch: {
    count(a) {
      if (a < 0) {
        this.count = 0
      }
    }
  },
  methods: {
    getT() {
      window.alert(this.$refs.scroller.getScrollTop())
    },
    setT() {
      this.$refs.scroller.setScrollTop(0)
    }
  }
}
</script>


